<template>
  <div class="container">
    <div class="top">
      <!-- <label>第二人格计分系统 1.0</label> -->
      <button type="button" data-toggle="modal" data-target="#reset" class="btn btn-default">重置</button>
    </div>

    <a
      tabindex="0"
      class="btn score"
      role="button"
      data-placement="bottom"
      data-toggle="popover"
      data-trigger="focus"
      title="分值意义"
      data-content="未来的薪资待遇、升职空间、竞争力！"
    >{{score}}</a>
    <ul class="history">
      <li v-for="(item, index) in history" :key="index">
        <label>{{item.scoreType=='add'?'加':'减'}}</label>
        <label>{{item.scoreValue+'分'}}</label>
        <label>{{item.scoreContent}}</label>
        <label>{{item.scoreTime}}</label>
      </li>
    </ul>
    <button
      type="button"
      data-toggle="modal"
      data-target="#score"
      class="btn btn-primary remove-score"
      @click="changeScoreType('remove')"
    >{{scoreContents.remove.text}}</button>
    <button
      type="button"
      data-toggle="modal"
      data-target="#score"
      class="btn btn-primary add-score"
      @click="changeScoreType('add')"
    >{{scoreContents.add.text}}</button>

    <!-- Modal -->
    <div class="modal fade" id="score" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="myModalLabel">{{scoreContents[scoreType].text}}</h5>
          </div>
          <div class="modal-body">
            <ul class="list-group">
              <li
                class="list-group-item"
                v-for="(item,index) in scoreContents[scoreType].contents"
                :key="index"
              >
                <input
                  type="radio"
                  :id="index"
                  name="scoreType"
                  :value="item"
                  v-model="scoreContent"
                />
                <label :for="index">{{item}}</label>
              </li>
              <li class="list-group-item">
                <select v-model="scoreValue">
                  <option value="1">1分</option>
                  <option value="2">2分</option>
                  <option value="3">3分</option>
                  <option value="4">4分</option>
                  <option value="5">5分</option>
                </select>
              </li>
            </ul>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="save">保存</button>
          </div>
        </div>
      </div>
    </div>

    <div class="modal fade" id="reset" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="myModalLabel">提示</h5>
          </div>
          <div class="modal-body">确定重置数据吗？</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="reset()">确定</button>
          </div>
        </div>
      </div>
    </div>

    <object id="fileObj" classid="clsid:10946843-7507-44FE-ACE8-2B3483D179B7" width="0" height="0"></object>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      score: 0,
      scoreContents: {
        add: {
          text: "加分",
          contents: ["工作", "学习", "健身", "早睡", "早起", "其他"]
        },
        remove: {
          text: "减分",
          contents: ["晚睡", "晚起", "懒惰", "放纵", "其他"]
        }
      },
      scoreType: "add",
      scoreValue: 1,
      scoreContent: "其他",
      history: [],
      url: "https://personalityscore.firebaseio.com/score.json"
    };
  },
  mounted() {
    this.getScore();

    this.$('[data-toggle="popover"]').popover();
  },
  methods: {
    getScore: function() {
      this.$http.get(this.url).then(res => {
        this.score = res.data.score;
        this.history = res.data.history;
      });

      // this.$http.get("../../../static/score.json").then(res => {
      //   this.score = res.data.score;
      //   this.history = res.data.history;
      // });
    },
    changeScoreType: function(type) {
      this.scoreType = type;
    },
    save: function() {
      var me = this;
      me.history = me.history || [];
      if (me.history.length == 10) {
        me.history.shift();
      }
      me.history.push({
        scoreType: me.scoreType,
        scoreValue: me.scoreValue,
        scoreContent: me.scoreContent,
        scoreTime: new Date().toLocaleDateString()
      });

      me.score =
        me.scoreType == "add"
          ? me.score + parseInt(me.scoreValue)
          : me.score - parseInt(me.scoreValue);

      me.$http
        .put(me.url, {
          score: me.score,
          history: me.history
        })
        .then(function(data) {
          me.getScore();
        });
    },
    reset: function() {
      var me = this;
      me.$http
        .put(me.url, {
          score: 0,
          history: []
        })
        .then(function(data) {
          me.getScore();
        });
    }
  }
};
</script>

<style>
.remove-score {
  position: absolute;
  left: 10px;
  bottom: 10px;
}
.add-score {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.score {
  color: brown !important;
  font-size: 60px !important;
}
.history {
  list-style-type: none;
  padding: 0px 50px;
  margin-top: 20px;
}
.history li > label:last-of-type {
  margin-right: 0;
}
.history li > label:first-of-type {
  margin-right: 0;
}

.history li > label {
  margin-right: 20px;
}

.top {
  padding-top: 10px;
  margin-bottom: 60px;
}
.top label {
  float: left;
}
.top button {
  float: right;
}

.modal-body {
  font-size: 20px;
}
.list-group-item label {
  line-height: 20px;
  margin: 0;
}
.list-group-item input {
  line-height: 20px;
  width: 20px;
  height: 20px;
  margin-right: 30px;
  vertical-align: middle;
  margin-bottom: 3px;
}

.list-group-item select {
  border: none;
  line-height: 20px;
  width: 120px;
}

.popover-body {
  font-size: 17px;
}
</style>


